import './List.css';

const List = ({ todos,deleteTodo,EditTodo }) => {
  // console.log({EditTodo})
  return (
    <>
      <ul>
        {todos.map((todos, index) => {
          //  console.log({todos})
          return (
              <li key={index} id={index} className="items">{todos}
                <button key={index} onClick={()=> deleteTodo(index)} id={index} className="delete-btn">Delete</button>
                <button type="text" key={index} onClick={()=> EditTodo(index)} id={index} className="edit-btn">Edit</button>
              </li>
          )
        })}
      </ul>
     </>
  )
}

export default List;
