import React from 'react';
import Input from './Components/Input/Input';
import Button from './Components/Button/Button';
import List from './Components/List/List';
import { v4 as uuidv4 } from 'uuid';
import './App.css'

class App extends React.Component {

  constructor() {
    super();

    this.state = {
      todos: [],
      input: '',
      todo: '',
      id: uuidv4() 
    }
  }
  onInputChange = (event) => {
    this.setState({
      input: event.target.value,
    })
  }


  addItem = () => {
   
    let todosLength = this.state.todos.length;
    if(this.state.input === "") {
      return;
    }

    const newTodos = [...this.state.todos];
    newTodos.push(this.state.input);

    // console.log(newTodos)

    this.setState({
      todos: "",
      todos: newTodos,
      input: '',
    });
  }


  deleteTodo = (index) => {
    const newTodos = this.state.todos.filter((todo, i) => {
      if (index === i) {
        return false;
      }
      return true;
    })
    this.setState({
      todos: newTodos
    });
  };

  EditTodo = (index) => {
    // console.log(id)
    const newTodos = prompt('Let make something change');
    console.log(newTodos);
    const todos = this.state.todos.map((todo, id) => {
         if (index === id) {
        return newTodos;
        }
        return todo;
    });
    
    // console.log(todos.innerHtml);
    this.setState({
      todos
    });
  }

  render() {
    return (
      <div className="container">
        <h1 className="heading">ToDo App</h1>
        <Input onInputChange={this.onInputChange} />
        <br />
        <Button addItem={this.addItem} />
        <br />
        <List todos={this.state.todos} deleteTodo={this.deleteTodo} EditTodo={this.EditTodo} />
      </div>
    )
  }
}

export default App